<template>
  <el-card>
    <el-table :data="tableData" stripe style="width: 100%" max-height="580" border>
      <el-table-column
        prop="index_left"
        label="序号"
        width="100px"
        header-align="center"
        align="center"
      ></el-table-column>
      <el-table-column prop="name_left" label="组织" header-align="center" align="center"></el-table-column>
      <el-table-column prop="id_left" label="id" header-align="center" align="center"></el-table-column>
      <el-table-column
        prop="index_right"
        label="序号"
        width="100px"
        header-align="center"
        align="center"
      ></el-table-column>
      <el-table-column prop="name_right" label="组织" header-align="center" align="center"></el-table-column>
      <el-table-column prop="id_right" label="id" header-align="center" align="center"></el-table-column>
    </el-table>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      tableData: []
    };
  },
  mounted() {
    var tableData = [];
    this.$get("offices").then(res => {
      var data = res.branchCompanys;
      if (data.length % 2 == 0) {
        for (let i = 0; i < data.length / 2; i++) {
          tableData.push({
            index_left: i + 1,
            id_left: data[i].id,
            name_left: data[i].name,
            index_right: i + data.length / 2 + 1,
            id_right: data[i + data.length / 2].id,
            name_right: data[i + data.length / 2].name
          });
        }
      } else {
        for (let i = 0; i < (data.length - 1) / 2; i++) {
          tableData.push({
            index_left: i + 1,
            id_left: data[i].id,
            name_left: data[i].name,
            index_right: i + (data.length - 1) / 2 + 1,
            id_right: data[i + (data.length - 1) / 2].id,
            name_right: data[i + (data.length - 1) / 2].name
          });
        }
        tableData.push({
          index_left: data.length,
          id_left: data[data.length - 1].id,
          name_left: data[data.length - 1].name
        });
      }
      this.tableData = tableData;
    });
  }
};
</script>

<style>
</style>


